"use client"

import { useEffect, useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { fetchTickets, type Ticket, PROBLEM_CATEGORIES, SECTORS } from "@/lib/api"
import Link from "next/link"

export default function AdminTicketsPage() {
  const [tickets, setTickets] = useState<Ticket[]>([])
  const [loading, setLoading] = useState(true)
  const [searchTerm, setSearchTerm] = useState("")

  useEffect(() => {
    const loadTickets = async () => {
      try {
        const data = await fetchTickets()
        setTickets(data)
      } catch (error) {
        console.error("Error loading tickets:", error)
      } finally {
        setLoading(false)
      }
    }

    loadTickets()
  }, [])

  const filteredTickets = tickets.filter(ticket =>
    PROBLEM_CATEGORIES.find(cat => cat.value === ticket.problema)?.label.toLowerCase().includes(searchTerm.toLowerCase()) ||
    ticket.nome.toLowerCase().includes(searchTerm.toLowerCase()) ||
    SECTORS.find(sec => sec.value === ticket.setor)?.label.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const getStatusColor = (status: string) => {
    switch (status) {
      case "aberto":
        return "bg-green-100 text-green-800"
      case "em_andamento":
        return "bg-yellow-100 text-yellow-800"
      case "fechado":
        return "bg-gray-100 text-gray-800"
      default:
        return "bg-gray-100 text-gray-800"
    }
  }

  const getPriorityColor = (prioridade: string) => {
    switch (prioridade) {
      case "alta":
        return "bg-red-100 text-red-800"
      case "media":
        return "bg-orange-100 text-orange-800"
      case "baixa":
        return "bg-blue-100 text-blue-800"
      default:
        return "bg-gray-100 text-gray-800"
    }
  }

  const getProblemLabel = (problemValue: string) => {
    return PROBLEM_CATEGORIES.find(cat => cat.value === problemValue)?.label || problemValue
  }

  const getSectorLabel = (sectorValue: string) => {
    return SECTORS.find(sec => sec.value === sectorValue)?.label || sectorValue
  }

  return (
    <div className="p-8 bg-background min-h-screen">
      <div className="max-w-6xl mx-auto text-foreground">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-3xl font-bold">Lista de Chamados</h1>
          <div className="flex gap-4">
            <Link href="/">
              <Button variant="outline">Voltar para Home</Button>
            </Link>
          </div>
        </div>

        <div className="mb-6">
          <Input
            type="text"
            placeholder="Pesquisar por problema, nome ou setor..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="max-w-md"
          />
        </div>

        {loading ? (
          <div className="text-center py-8">Carregando chamados...</div>
        ) : (
          <div className="grid gap-4">
            {filteredTickets.map((ticket) => (
              <Card key={ticket.id} className="hover:shadow-md transition-shadow">
                <CardContent className="p-6">
                  <div className="flex justify-between items-start">
                    <div>
                      <h3 className="text-xl font-semibold mb-2">{getProblemLabel(ticket.problema)}</h3>
                      <p className="text-gray-600 mb-4">{ticket.descricao}</p>
                      <div className="flex gap-2 items-center text-sm">
                        <span className="text-gray-500">Por: {ticket.nome}</span>
                        <span className="text-gray-400">•</span>
                        <span className="text-gray-500">Setor: {getSectorLabel(ticket.setor)}</span>
                        <span className="text-gray-400">•</span>
                        <span className="text-gray-500">
                          {new Date(ticket.dataCriacao).toLocaleDateString('pt-BR')}
                        </span>
                      </div>
                    </div>
                    <div className="flex flex-col gap-2">
                      <span className={`px-3 py-1 rounded-full text-sm ${getStatusColor(ticket.status)}`}>
                        {ticket.status.replace('_', ' ')}
                      </span>
                      <span className={`px-3 py-1 rounded-full text-sm ${getPriorityColor(ticket.prioridade)}`}>
                        {ticket.prioridade}
                      </span>
                    </div>
                  </div>
                  <div className="mt-4 flex justify-end">
                    <Link href={`/admin/tickets/${ticket.id}`}>
                      <Button variant="outline">Ver Detalhes</Button>
                    </Link>
                  </div>
                </CardContent>
              </Card>
            ))}

            {filteredTickets.length === 0 && (
              <div className="text-center py-8 text-gray-500">
                Nenhum chamado encontrado.
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  )
}
